"use client"

import { useState, useEffect } from "react"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Edit, Trash2, Search, Utensils, MapPin } from "lucide-react"
import Link from "next/link"
import Image from "next/image"
import { fetchFromApi } from "@/lib/data"
import { StatusBadge } from "@/components/status-badge"

export function UmkmDashboard() {
  const [items, setItems] = useState<any[]>([])
  const [search, setSearch] = useState("")
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    loadMyCulinaries()
  }, [])

  const loadMyCulinaries = async () => {
    // Ambil data asli milik UMKM yang sedang login dari Laravel
    const res = await fetchFromApi<any[]>("/my-culinaries")
    setItems(res || [])
    setLoading(false)
  }

  const handleDelete = async (id: string) => {
    if (confirm("Hapus kuliner ini dari daftar?")) {
      // Logic delete via API akan kita tambahkan di step integrasi CRUD nanti
      alert("Fitur hapus sedang disinkronkan ke database.")
    }
  }

  const filtered = items.filter((item) =>
    item.nama.toLowerCase().includes(search.toLowerCase())
  )

  if (loading) return <div className="p-10 text-center font-serif text-[#4e5b31]">Memuat Daftar Menu Anda...</div>

  if (items.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center min-h-[400px] bg-white rounded-[40px] border-2 border-dashed border-gray-200">
        <Utensils className="text-gray-200 mb-6" size={80} />
        <h2 className="text-2xl font-bold text-[#3b2f2f] mb-2">Menu Masih Kosong</h2>
        <p className="text-[#6e5849] mb-8 text-center max-w-xs">Anda belum menambahkan hidangan apa pun ke portal ini.</p>
        <Link href="/umkm/tambah">
          <Button className="bg-[#4e5b31] hover:bg-[#3a4323] px-10 py-6 rounded-2xl text-lg font-bold">Mulai Jualan Sekarang</Button>
        </Link>
      </div>
    )
  }

  return (
    <div className="space-y-8 font-sans">
      {/* Filter & Search Dashboard */}
      <div className="flex flex-col md:flex-row gap-4">
        <div className="relative flex-1 group">
          <Search className="absolute left-4 top-3 text-gray-400 group-focus-within:text-[#4e5b31] transition-colors" />
          <Input
            placeholder="Cari menu Anda..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            className="pl-12 py-6 rounded-2xl border-gray-200 bg-white"
          />
        </div>
      </div>

      {/* Grid Menu UMKM */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filtered.map((item) => (
          <Card key={item.id} className="rounded-[32px] overflow-hidden bg-white border-none shadow-sm hover:shadow-xl transition-all">
            <div className="relative h-48">
              <Image 
                src={item.images[0] || "https://i.pinimg.com/736x/ab/0d/f7/ab0df7146af3fe5c29d1513cdaf9b97e.jpg"} 
                alt={item.nama} 
                fill 
                className="object-cover" 
              />
              <div className="absolute top-4 right-4">
                <StatusBadge status={item.status} />
              </div>
            </div>

            <div className="p-6 space-y-4">
              <div>
                <h3 className="font-bold text-xl text-[#3b2f2f] line-clamp-1">{item.nama}</h3>
                <p className="text-xs text-[#6e5849] flex items-center gap-1 mt-1">
                  <MapPin size={12} /> {item.kota}, {item.provinsi}
                </p>
              </div>

              <div className="flex gap-2">
                <Link href={`/umkm/${item.id}/edit`} className="flex-1">
                  <Button variant="outline" className="w-full rounded-xl border-[#ddd] font-bold gap-2">
                    <Edit size={16} /> Edit
                  </Button>
                </Link>
                <Button
                  onClick={() => handleDelete(item.id)}
                  variant="outline"
                  className="rounded-xl border-red-100 text-red-500 hover:bg-red-50"
                >
                  <Trash2 size={16} />
                </Button>
              </div>
            </div>
          </Card>
        ))}
      </div>
    </div>
  )
}